html, body {
	height: 100%;
  background: linear-gradient(
    #fdbaf8, #7868e6
  );
  text-align: center;
  margin: 0;
}

* {
	font-family: Helvetica, Arial, sans-serif;
  box-sizing: border-box;
}

#app {
	position: relative;
	height: 100%;
	width: 100%;
	padding: 8px;
}

h1 {
  margin: 8px 0;
}

form {
  display: flex;
  flex-direction: column;
  position: relative;
}

input,
textarea {
  padding: 5px;
}
form {
  margin-top: 40px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  
}

form input {
  padding: 10px;
  font-size: 16px;
  border: 2px solid #FFF;
  width: 400px;
}
form button {
   position: absolute;
  top: 0;
  right: 0;
  padding: 5px; 
  padding: 10px;
    font-size: 16px;
    margin: 10px;
    margin-right: 174px;
    margin-top: 0px;
    background-color: #0066FF;
    color: #FFF;
    border: 2px solid #0066FF;
}

button:hover {
  background-color: #7c0a99;
  border: 2px solid #ef6ff3;
  cursor: pointer;
}

ul {
  list-style: none;
  text-align: center;
}

ul {
  padding: 0;
}

li {
  background-color:linear-gradient(
     #7868e6, #fdbaf8
  );
  border: 1px solid rgb(204, 67, 238);
  border-radius: 5px 2px;
  margin: 8px 0px;
  width: 400px;
}

li div {
  position: relative;
}

li div button {
  position: absolute;
  top: 0;
  right: 0;
  background-color: transparent;
  border: 0;
  padding: 10px;
  font-weight: bolder;
  cursor: pointer;
  border-radius: 5px;
}

li div button:hover,
li div button:focus {
  box-shadow: 0 0 1px rgb(138, 4, 127);
}

li h2 {
  background-color: linear-gradient(
    #fdbaf8, #7868e6
  );
  margin: 0;
  padding: 4px;
}

li p {
  margin: 8px;
}